// dependencies
import { showLoader, hideLoader, initHeader, initFooter, initBreadcrumbs, parseApiError, getCookie, onClick, getSiteId, link } from '@kenzap/k-cloud';
import { HTMLContent } from "../_/_cnt_home.js"

// where everything happens
const _this = { 

    state: {
        firstLoad: true,
        ajaxQueue: 0
    },
    init: () => {
        
        _this.getData(); 
    },
    getData: () => {

        // show loader during first load
        if (_this.state.firstLoad) showLoader();

        // do API query
        fetch('https://api-v1.kenzap.cloud/', {
            method: 'post',
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'text/plain',
                'Authorization': 'Bearer ' + getCookie('kenzap_api_key'),
                'Kenzap-Header': localStorage.hasOwnProperty('header'),
                'Kenzap-Token': getCookie('kenzap_token'),
                'Kenzap-Sid': getSiteId(),
            },
            body: JSON.stringify({
                query: {
                    locale: {
                        type:       'locale',
                        id:         getCookie('lang')
                    }
                }
            })
        })
        .then(response => response.json())
        .then(response => {

            // hide UI loader
            // hideLoader();

            if(response.success){

                // init header
                initHeader(response);

                _this.getLayouts(response);

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    },
    getLayouts: (response) => {

        // do API query
        fetch('https://api.kenzap.com/myticket/?cmd=get_layouts', {
            method: 'get',
            headers: {
                'Accept': 'application/json',
                'Content-type': 'text/plain',
                'Kenzap-Header': localStorage.hasOwnProperty('header'),
                'Kenzap-Token': getCookie('kenzap_token'),
                'Kenzap-Sid': getSiteId(),
            },
            // body: params
        })
        .then(layouts_res => layouts_res.json())
        .then(layouts_res => {

            // hide UI loader
            // hideLoader();

            console.log(layouts_res);
            // return;

            if(layouts_res.success){

                // get core html content 
                _this.loadHomeStructure();  

                // render table
                _this.renderPage(layouts_res);

                // listeners
                _this.initListeners();

                // initiate footer
                _this.initFooter();

                // first load
                _this.state.firstLoad = false;

            }else{

                parseApiError(response);
            }
        })
        .catch(error => {
            console.error('Error:', error);
        });
    },
    renderPage: (data) => {

        let d = document;

        // initiate breadcrumbs
        initBreadcrumbs(
            [
                { link: link('https://dashboard.kenzap.cloud?launcher=myticket'), text: __('Dashboard') },
                { text: __('MyTicket') },
            ]
        );

        // render table
        let layout_list = '', layouts_i = 0;

        // generate website table
        for(var i in data.layouts){

            layouts_i++;

            // only preview assets that are not marked as removed
            layout_list += `
            <tr>
                <td>${ (parseInt(i)+1) }</td>
                <td><a href="${ link('/layout-edit/?id='+data.layouts[i].id ) }">${ data.layouts[i].title }</a></td>
                <td style="display:none;">${ data.layouts[i].count_zones }</td>
                <td style="display:none;">${ data.layouts[i].count_seats }</td>
                <td style="text-align:right;" class="">
                    <button class="btn btn-sm dropdown-toggle" type="button" id="dropdownMenuSizeButton${i}" data-i="${i}" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">${ __('Option') }</button>
                    <div class="dropdown-menu" aria-labelledby="dropdownMenuSizeButton${i}" >
                        <a class="dropdown-item edit-layout" href="${ link('/layout-edit/?id='+data.layouts[i].id ) }" target="_self" >${ __('Edit') }</a>
                        <a class="dropdown-item duplicate-layout" href="#" data-toggle="modal" data-id="${ data.layouts[i].id }" >${ __('Duplicate') }</a>
                        <a class="dropdown-item rename-layout" href="#" data-toggle="modal" data-id="${ data.layouts[i].id }" data-title="${ data.layouts[i].title }">${ __('Rename') }</a>
                        <div class="dropdown-divider"></div>
                        <a class="dropdown-item remove-layout" href="#" data-toggle="modal" data-id="${ data.layouts[i].id }" >${ __('Remove') }</a>
                    </div>
                </td>
            </tr>`;
        }

        if(layouts_i==0){

            d.querySelector(".layout_list").innerHTML = `<tr><td>${ __('No layouts to display yet') }</td><td></td><td></td><td></td><td></td></tr>`;
        }else{

            d.querySelector(".layout_list").innerHTML = layout_list;
        }

        hideLoader();
    },
    initListeners: () => {

        console.log('initListeners ');

        // rename layout
        onClick('.rename-layout', _this.listeners.renameLayout);

        // duplicate layout
        onClick('.duplicate-layout', _this.listeners.duplicateLayout);

        // rename layout
        onClick('.remove-layout', _this.listeners.removeLayout);

        // prevents listeners to be assigned twice
        if(!_this.state.firstLoad) return;

        // add layout listener
        onClick('.btn-add', _this.listeners.addLayout);

        // add product confirm
        onClick('.btn-modal', _this.listeners.modalSuccessBtn);
    },
    listeners: {

        addLayout: (e) => {

            e.preventDefault();
            
            let modal = document.querySelector(".modal");
            let modalCont = new bootstrap.Modal(modal);
            
            modal.querySelector(".modal-title").innerHTML = __('Add Layout');
            modal.querySelector(".btn-primary").innerHTML = __('Add');
            modal.querySelector(".btn-secondary").innerHTML = __('Cancel');
            let id = e.currentTarget.dataset.id;
            let title = "";
            let modalHTml = `
            <div class="form-cont">
                <div class="form-group mb-3">
                    <label for="p-title" class="form-label">${ __('Title') }</label>
                    <input type="text" class="form-control" id="p-title" autocomplete="off" placeholder="${ __('Arena..') }" value="${ title }">
                    <ol class="mt-3 form-text">
                        <li>Enter layout title</li>
                        <li>Click add button</li>
                        <li>Start creating layout</li>
                    </ol>
                </div>\
                <p class="mt-2 form-text fs-8">Need help in designing your hall or stadium? <a target="_blank" href="https://kenzap.com/seat-reservation-in-wordpress-setup-service-1014779/">View examples</a>.</p>
            </div>`;
    
            modal.querySelector(".modal-body").innerHTML = modalHTml;

            _this.listeners.modalSuccessBtnFunc = (e) => {

                e.preventDefault();
    
                let data = {};
                data.title = modal.querySelector("#p-title").value;

                if(data.title.length<4){ alert(__('Please provide longer title')); return; }
    
                // send data
                let params = new URLSearchParams();
                params.append("cmd", "add_layout");
                params.append("title", data.title);

                // do API query
                showLoader();

                fetch('https://api.kenzap.com/myticket/', {
                    method: 'post',
                    headers: {
                        'Accept': 'application/json',
                        'Content-type': 'application/x-www-form-urlencoded',
                        'Authorization': 'Bearer ' + getCookie('kenzap_api_key'),
                        'Kenzap-Header': localStorage.hasOwnProperty('header'),
                        'Kenzap-Token': getCookie('kenzap_token'),
                        'Kenzap-Sid': getSiteId(),
                    },
                    body: params
                })
                .then(response => response.json())
                .then(response => {
    
                    if (response.success){
    
                        modalCont.hide();
                        
                        _this.getData();

                    }else{
    
                        parseApiError(response);
                    }
                })
                .catch(error => {
                    console.error('Error:', error);
                });
            }

            modalCont.show();

            setTimeout( () => modal.querySelector("#p-title").focus(),100 );
        },
        duplicateLayout: (e) => {

            e.preventDefault();

            if(!confirm(__('Duplicate this layout?'))) return;

            let id = e.currentTarget.dataset.id;

            e.preventDefault();

            // send data
            let params = new URLSearchParams();
            params.append("cmd", "duplicate_layout");
            params.append("id", id);

            // do API query
            showLoader();

            fetch('https://api.kenzap.com/myticket/', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-type': 'application/x-www-form-urlencoded',
                    'Authorization': 'Bearer ' + getCookie('kenzap_api_key'),
                    'Kenzap-Header': localStorage.hasOwnProperty('header'),
                    'Kenzap-Token': getCookie('kenzap_token'),
                    'Kenzap-Sid': getSiteId(),
                },
                body: params
            })
            .then(response => response.json())
            .then(response => {

                if (response.success){
    
                    _this.getData();

                }else{

                    parseApiError(response);
                }
            })
            .catch(error => {
                console.error('Error:', error);
            });
        },
        removeLayout: (e) => {

            e.preventDefault();

            if(!confirm(__('Permanently remove this layout?'))) return;

            let id = e.currentTarget.dataset.id;

            e.preventDefault();

            // send data
            let params = new URLSearchParams();
            params.append("cmd", "remove_layout");
            params.append("id", id);

            // do API query
            showLoader();

            fetch('https://api.kenzap.com/myticket/', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-type': 'application/x-www-form-urlencoded',
                    'Authorization': 'Bearer ' + getCookie('kenzap_api_key'),
                    'Kenzap-Header': localStorage.hasOwnProperty('header'),
                    'Kenzap-Token': getCookie('kenzap_token'),
                    'Kenzap-Sid': getSiteId(),
                },
                body: params
            })
            .then(response => response.json())
            .then(response => {

                if (response.success){
    
                    _this.getData();

                }else{

                    parseApiError(response);
                }
            })
            .catch(error => {
                console.error('Error:', error);
            });
        
        },
        renameLayout: (e) => {

            e.preventDefault();
            
            let modal = document.querySelector(".modal");
            let modalCont = new bootstrap.Modal(modal);
            
            modal.querySelector(".modal-title").innerHTML = __('Rename Layout');
            modal.querySelector(".btn-primary").innerHTML = __('Rename');
            modal.querySelector(".btn-secondary").innerHTML = __('Cancel');
            let id = e.currentTarget.dataset.id;
            let title = e.currentTarget.dataset.title;
            let modalHTml = `\

            <div class="form-cont">\
                <div class="form-group mb-3">\
                    <label for="p-title" class="form-label">${ __('Title') }</label>\
                    <input type="text" class="form-control" id="p-title" autocomplete="off" placeholder="" value="${ title }">\
                </div>\
            </div>`;
    
            modal.querySelector(".modal-body").innerHTML = modalHTml;

            _this.listeners.modalSuccessBtnFunc = (e) => {

                e.preventDefault();
    
                let data = {};
                data.title = modal.querySelector("#p-title").value;

                if(data.title.length<4){ alert(__('Please provide longer title')); return; }
    
                // send data
                let params = new URLSearchParams();
                params.append("cmd", "rename_layout");
                params.append("title", data.title);
                params.append("id", id);

                // do API query
                showLoader();

                fetch('https://api.kenzap.com/myticket/', {
                    method: 'post',
                    headers: {
                        'Accept': 'application/json',
                        'Content-type': 'application/x-www-form-urlencoded',
                        'Authorization': 'Bearer ' + getCookie('kenzap_api_key'),
                        'Kenzap-Header': localStorage.hasOwnProperty('header'),
                        'Kenzap-Token': getCookie('kenzap_token'),
                        'Kenzap-Sid': getSiteId(),
                    },
                    body: params
                })
                .then(response => response.json())
                .then(response => {
    
                    if (response.success){
    
                        modalCont.hide();
                        
                        _this.getData();

                    }else{
    
                        parseApiError(response);
                    }
                })
                .catch(error => {
                    console.error('Error:', error);
                });
            }

            modalCont.show();

            setTimeout( () => modal.querySelector("#p-title").focus(),100 );
        },
        modalSuccessBtn: (e) => {
            
            _this.listeners.modalSuccessBtnFunc(e);
        },

        modalSuccessBtnFunc: null
    },
    loadHomeStructure: () => {

        if(!_this.state.firstLoad) return;

        // get core html content 
        document.querySelector('#contents').innerHTML = HTMLContent(__);
    },
    initFooter: () => {
        
        initFooter(__('Copyright Â© '+new Date().getFullYear()+' <a class="text-muted" href="https://kenzap.com/" target="_blank">Kenzap</a>. All rights reserved.'), __('Kenzap Cloud Services - Dashboard'));
    }
}

_this.init();
